.catalog {
  display: flex;
  flex-wrap: wrap;

  max-width: 944px;
  margin: 0 auto;
  padding: 50px 0;

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    transition-duration: var(--animation-duration);
    width: 200px;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 48px;

    &:not(:nth-child(4n)) {
      margin-right: 48px;
    }
  }
  &__card:hover {
    transform: scale(120%);
    color: #34568b;
  }
}
